/**
 * 
 */
export default class Tips {
    constructor() {
      this.isLoading = false;
    }
    /**
     * 
     */
  
    static success(title, duration = 500) {
      setTimeout(() => {
        wx.showToast({
          title: title,
          icon: "success",
          mask: true,
          duration: duration
        });
      }, 300);
      if (duration > 0) {
        return new Promise((resolve, reject) => {
          setTimeout(() => {
            resolve();
          }, duration);
        });
      }
    }
  
    /**
     * 
     */
    static confirm(text, title, payload) {
      return new Promise((resolve, reject) => {
        wx.showModal({
          content: text,
          confirmText: title || '',
          cancelText: '',
          showCancel: true,
          success: res => {
            if (res.confirm) {
                resolve();
            } else if (res.cancel) {
                reject();
            }
          }
        });
      });
    }
  
    /**
     * 
     */
    static alertDialog(text, confirmText, cancelText, payload = {}, title = "") {
      return new Promise((resolve, reject) => {
        wx.showModal({
          content: text,
          confirmText: confirmText || '',
          cancelText: cancelText || '',
          showCancel: !!cancelText,
          success: res => {
            if (res.confirm) {
              resolve(payload);
            } else if (res.cancel) {
              reject(payload);
            }
          },
          fail: res => {
            reject(payload);
          }
        });
      });
    }
  
    static toast(title, icon = "success", onHide ) {
      setTimeout(() => {
        wx.showToast({
          title: title,
          icon: icon,
          mask: true,
          duration: 2000
        });
      }, 300);
  
      // 
      if (onHide) {
        setTimeout(() => {
          onHide();
        }, 500);
      }
    }
  
    /**
     * 
     */
    static alert(title) {
      wx.showToast({
        title: title,
        mask: true,
        duration: 1500
      });
    }
  
    /**
     * 
     */
  
    static error(title, onHide) {
      wx.showToast({
        title: title,
        mask: true,
        duration: 500
      });
      // 
      if (onHide) {
        setTimeout(() => {
          onHide();
        }, 500);
      }
    }
  
    /**
     * 
     */
    static loading(title = "") {
      if (Tips.isLoading) {
        return;
      }
      Tips.isLoading = true;
      wx.showLoading({
        title: title,
        mask: true
      });
    }
  
    /**
     * 
     */
    static loaded() {
      if (Tips.isLoading) {
        Tips.isLoading = false;
        wx.hideLoading();
      }
    }
  
    static share(title, url, desc) {
      return {
        title: title,
        path: url,
        desc: desc,
        success: function(res) {
          Tips.toast("");
        }
      };
    }
  }
  
  /**
   * 
   */
  Tips.isLoading = false;
  